.exp-cards {
  padding: 4rem;
  background: #242424;
}

.exp-header{
  text-align: center;
  color: #4162e8;
}

.exp-cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.exp-cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.exp-cards-item-pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.exp-cards-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.exp-cards-item-img:hover{
  transform: scale(0.95);
}

.fa-java {
  color: #fff;
  margin-bottom: 20px;
  font-size: 64px;
}

.fa-python {
  color: #fff;
  margin-bottom: 20px;
  font-size: 64px;
}

.exp-icon-container{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.exp-cards-items {
  margin-bottom: 24px;
}

.exp-cards-item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.exp-cards-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.exp-cards-item-info {
  padding: 20px 30px 30px;
}

.exp-cards-item-title {
  color: #4162e8;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}

.exp-cards-item-description {
  color: #8c8c8c;
  margin-top: 20px;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

.exp-icon-links{
  display: flex;
  width: 100%;
  margin-top: 15px;
  justify-content: center;
}

@media only screen and (min-width: 1200px) {
  .exp-content-blog-container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .exp-cards-items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .exp-cards-item {
    margin-bottom: 2rem;
  }
}
